import { Icons } from '../../../../assets/SvgComponents/SvgIcons'
import {
  BigScreen,
  getGlobalStyle,
  SmallScreen
} from '../../../../helpers'
import {
  Color,
  Icon,
  Text
} from '../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../layout'
import styles from './StoreLocatorHeader.module.css'

export interface StoreLocatorHeaderProps {
  addressSelected?: string;
  iconStoreLocator?: keyof (typeof Icons);
  isLoggedIn?: boolean;
  isMobile?: boolean;
  isPickup?: boolean;
  loggedOutText?: string;
  site?: 'unimarc' | 'alvi';
  smallScreenSLVisible?: boolean;
  timeTable?: string;
  size?: 'md' | 'lg';
  onClickStoreLocator: () => void;
}

export const StoreLocatorHeader = ({
  addressSelected = '',
  iconStoreLocator = 'Location',
  isLoggedIn,
  isMobile,
  isPickup,
  loggedOutText = 'Selecciona tu dirección',
  site = 'unimarc',
  smallScreenSLVisible = true,
  timeTable = '',
  size = 'md',
  onClickStoreLocator
}: StoreLocatorHeaderProps) => {
  const siteUnimarc = site === 'unimarc'
  const customSize = siteUnimarc ? 26 : 24
  const customSizeDropdownIcon = siteUnimarc ? 10 : 11
  const fontSize = size === 'lg' ? 'sm' : 'md'

  const setColor = (): Color => {
    if (size === 'lg') return 'black'
    return 'white'
  }

  const setTruncate = () => {
    if (size === 'lg') return 100
    return 20
  }

  const setHeight = () => {
    if (size === 'lg') return '46px'
    return isLoggedIn ? '43px' : '29px'
  }

  const setFontWeight = () => {
    if (size === 'lg') return 'semibold'
    return 'regular'
  }

  const textColor = isMobile ? 'black' : setColor()
  const truncate = isMobile ? 30 : setTruncate()

  return (
    <>
      <BigScreen>
        <Container customWidth='max-content' minWidth='243px'>
          <Row
            alignItems='center'
            clickable='pointer'
            customHeight={size === 'lg' ? '46px' : ''}
          >
            {isLoggedIn &&
              <Row onClick={onClickStoreLocator}>
                <Column customWidth='max-content'>
                  <Icon
                    clickable='pointer'
                    color={setColor()}
                    customSize={22}
                    name={iconStoreLocator}
                    site={site}
                  />
                </Column>
                <Spacer.Vertical size={4} />
                <Column>
                  <Row alignItems='center' clickable='pointer'>
                    <Column customWidth='max-content'>
                      {size === 'lg' ? (
                        <Text
                          clickable='pointer'
                          color='black'
                          fontSize='md'
                          underline='hover'
                        >
                          {isPickup ? 'Retiro en tienda' : 'Despacho a domicilio'}
                        </Text>
                      ):(
                        <Text
                          clickable='pointer'
                          color={textColor}
                          fontSize={fontSize}
                          fontWeight='semibold'
                          truncate={truncate}
                          underline='hover'
                        >
                          {addressSelected}
                        </Text>
                      )}
                    </Column>
                    <Spacer.Vertical size={4} />
                    <Column customWidth='max-content'>
                      <Icon
                        clickable='pointer'
                        color={setColor()}
                        customSize={customSizeDropdownIcon}
                        name='DropDownClose'
                      />
                    </Column>
                  </Row>
                  <Row>
                    {size === 'lg' &&
                      <>
                        <Text
                          clickable='pointer'
                          color={textColor}
                          fontSize={fontSize}
                          fontWeight='semibold'
                          truncate={truncate}
                          underline='hover'
                        >
                          {addressSelected}
                        </Text>
                        <Spacer.Vertical size={4} /> - <Spacer.Vertical size={4} />
                        <Text
                          clickable='pointer'
                          color={textColor}
                          fontSize='sm'
                        >
                          {timeTable}
                        </Text>
                      </>
                    }
                    {(size === 'md' && timeTable) &&
                      <Text
                        clickable='pointer'
                        color={textColor}
                        fontSize='sm'
                      >
                        {timeTable}
                      </Text>
                    }
                  </Row>
                </Column>
              </Row>
            }
            {!isLoggedIn &&
              <Row
                alignItems='center'
                clickable='pointer'
                onClick={onClickStoreLocator}
              >
                <Column customWidth='max-content'>
                  <Icon
                    clickable='pointer'
                    color={setColor()}
                    customSize={customSize}
                    name={iconStoreLocator}
                    site={site}
                  />
                </Column>
                <Spacer.Vertical size={8} />
                <Column customWidth='max-content'>
                  <Text
                    clickable='pointer'
                    color={setColor()}
                    customClassName={styles['text-hover']}
                    fontSize='md'
                    fontWeight={setFontWeight()}
                  >
                    {loggedOutText}
                  </Text>
                </Column>
                <Spacer.Vertical size={8} />
                <Column customWidth='max-content'>
                  <Icon
                    clickable='pointer'
                    color={setColor()}
                    customSize={customSizeDropdownIcon}
                    name='DropDownClose'
                  />
                </Column>
              </Row>
            }
          </Row>
        </Container>
      </BigScreen>
      {smallScreenSLVisible &&
        <SmallScreen>
          <Container>
            <Row
              alignItems='center'
              backgroundColor={getGlobalStyle('--color-neutral-gray-white')}
              boxShadow={getGlobalStyle('--box-shadow-2xs')}
              customClassName={siteUnimarc ? '' : `${styles.wrapperRow}`}
              customHeight={setHeight()}
              padding='4px 18px'
              position='sticky'
              zIndex='1'
            >
              {isLoggedIn &&
                <Row onClick={onClickStoreLocator} >
                  <Column customWidth='max-content'>
                    <Spacer.Horizontal size={1} />
                    <Icon
                      clickable='pointer'
                      color={getGlobalStyle('--color-base-black')}
                      customSize={16}
                      name={iconStoreLocator}
                      site={site}
                    />
                  </Column>
                  <Spacer.Vertical size={2} />
                  <Column>
                    <Row
                      alignItems='center'
                      clickable='pointer'
                      justifyContent='between'
                    >
                      <Column customWidth='max-content'>
                        <Text
                          clickable='pointer'
                          color={textColor}
                          fontSize={fontSize}
                          fontWeight='semibold'
                          truncate={truncate}
                          underline='hover'
                        >
                          {addressSelected}
                        </Text>
                      </Column>
                      <Spacer.Vertical size={4} />
                    </Row>
                    <Row>
                      {timeTable &&
                        <Text
                          clickable='pointer'
                          color={textColor}
                          fontSize='sm'
                        >
                          {timeTable}
                        </Text>
                      }
                    </Row>
                  </Column>
                  <Column
                    customWidth='max-content'
                    justifyContent='center'
                    minHeight='34px'
                  >
                    <Icon
                      clickable='pointer'
                      color={getGlobalStyle('--color-text-gray-light')}
                      customSize={11}
                      name='DropDownClose'
                      site={site}
                    />
                  </Column>
                </Row>
              }
              {!isLoggedIn &&
                <Row
                  alignItems='center'
                  clickable='pointer'
                  onClick={onClickStoreLocator}
                >
                  <Row alignItems='center'>
                    <Column customWidth='max-content'>
                      <Icon
                        clickable='pointer'
                        color={getGlobalStyle('--color-base-black')}
                        customSize={14}
                        name={iconStoreLocator}
                        site={site}
                      />
                    </Column>
                    <Spacer.Vertical size={8} />
                    <Column customWidth='max-content'>
                      <Text
                        clickable='pointer'
                        fontSize='md'
                        fontWeight={'semibold'}
                      >
                        {loggedOutText}
                      </Text>
                    </Column>
                  </Row>
                  <Spacer.Vertical size={8} />
                  <Column customWidth='max-content'>
                    <Icon
                      clickable='pointer'
                      color={getGlobalStyle('--color-base-black')}
                      customSize={10}
                      name='DropDownClose'
                      site={site}
                    />
                  </Column>
                </Row>
              }
            </Row>
          </Container>
        </SmallScreen>
      }
    </>
  )
}